/* ================= BASE ================= */

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* ================= NAVBAR ================= */

.navbar-custom {
  font-size: large;
}

.navbar__link {
  margin-left: 0.75em;
  margin-right: 0.75em;
  font-size: 1em;
  cursor: pointer;
  text-decoration: none;
  letter-spacing: 0.1em;
  text-indent: 0.3em;
  border-bottom: 3px solid transparent;
}

.navbar__link::after {
  transition: all ease-in-out 0.2s;
  content: "";
  display: block;
  margin-top: 2px;
  height: 3px;
  width: 0;
}

.navbar__link:hover::after {
  width: 40%;
}

.navbar__link--active::after {
  width: 100%;
}

/* ================= MAIN ================= */

.MainApp {
  margin-top: 8vh;
  flex: 1;
}

.main {
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 8vh);
}

.section-content-container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
}

/* ================= TYPEWRITER ================= */

.Typewriter {
  display: inline-block;
}

.Typewriter__wrapper,
.Typewriter__cursor {
  font-size: xx-large;
  font-weight: bold;
}

/* ================= SOCIAL ================= */

.social {
  margin-top: 2.5rem;
}

/* ================= HEADER ================= */

.header {
  font-size: 3em;
  font-weight: bold;
  margin-bottom: 25px;
  text-align: center;
}

/* ================= ABOUT PAGE ================= */

.about-section {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 7rem 2rem 3rem;
}

.about-grid {
  max-width: 1100px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
}

.about-left h2,
.about-right h2 {
  margin-bottom: 1.5rem;
}

.about-left p {
  line-height: 1.75;
  margin-bottom: 1rem;
}

.education-item {
  margin-bottom: 1.5rem;
}

.education-item h3 {
  margin-bottom: 0.3rem;
}

/* ================= TIMELINE ================= */

section.timeline-card-content {
  justify-content: center;
}

section.timeline-card-content p.card-title {
  font-size: 1.5em;
  margin-top: 25px;
}

section.timeline-card-content p.card-sub-title {
  font-size: 1.25em;
  margin-top: 10px;
}

section.timeline-card-content div.card-description > p {
  font-size: 1.1em;
  margin-top: 10px;
  font-weight: 500;
}

/* ================= RESPONSIVE ================= */

@media (max-width: 900px) {
  .about-grid {
    grid-template-columns: 1fr;
    gap: 2.5rem;
  }
}

@media (max-width: 768px) {
  .navbar__link::after {
    display: none;
  }
}
/* ===== FIX FOR FIXED NAVBAR OVERLAP ===== */

.about-section {
  padding-top: calc(8vh + 3rem);
}
